.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #0e0e0e;
  text-align: center;
}

.title {
  font-size: 2.5rem;
  color: #ffffff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20vh;
}

input {
  padding: 0.5rem;
  margin: 0.5rem;
  width: 250px;
  font-size: 1rem;
}

button {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  cursor: pointer;
}
